html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  font-weight: 400;
  font-size: 15px;
  font-style: normal;
  line-height: 1.5;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  max-width: 1200px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 9999;
}

.header__inner {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: 22px 0;
}

.nav__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.nav__item:not(:last-child) {
  margin-right: 35px;
}

.nav__link {
  display: inline-block;
  position: relative;
  padding: 5px 0;
  color: var(--text-color-light);
  font-weight: 600;
  line-height: 1.46;
  text-decoration: none;
  text-transform: uppercase;
}

.nav__link--active::before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background-color: var(--text-color-light);
  z-index: 2;
}

.language__list {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

.language__item {
  display: inline-block;
  padding: 5px 0;
  color: var(--text-color-light);
  font-weight: 600;
  line-height: 1.46;
  text-transform: uppercase;
  cursor: pointer;
  opacity: 0.8;
}
.language__item:not(:last-child) {
  margin-right: 8px;
}

.language__item--active {
  opacity: 1;
}

.hero {
  position: relative;
  min-height: 750px;
  background: url("../../img/hero-bg.jpg") no-repeat center;
  background-size: cover;
}
.hero::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
}

.hero__inner {
  position: relative;
  padding: 217px 0;
  overflow: hidden;
}

.hero__title {
  width: 791px;
  margin-bottom: 53px;
  color: var(--text-color-light);
  font-weight: 700;
  font-size: 48px;
  line-height: 150%;
  text-transform: uppercase;
}

.btn__hero {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  width: 200px;
  height: 50px;
  padding: 12px;
  color: var(--text-color-light);
  font-weight: 500;
  font-size: 18px;
  background: var(--accent-color);
  overflow: hidden;
  transition: all 300ms ease-in-out;
}
.btn__hero:hover, .btn__hero:focus, .btn__hero:active {
  color: var(--accent-color);
  background: var(--bg-color-light);
}

.footer__inner {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  height: 150px;
}

.footer__copiright {
  color: var(--text-color-dark);
  letter-spacing: 0.15em;
}

.social {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

.social__item:not(:last-child) {
  margin-right: 21px;
}

.social__icon {
  width: 25px;
  height: 25px;
  transition: fill 300ms ease-in-out;
}

.social__link:hover .social__icon, .social__link:focus .social__icon, .social__link:active .social__icon {
  fill: var(--accent-color);
}